export default class Forms {

    constructor(formSelector) {
        try {
            this.forms = document.querySelectorAll(formSelector);
            this.inputs = document.querySelectorAll('input');
            this.messages = {
                loading: 'Waiting...',
                success: 'We got your message!',
                error: 'Something went wrong :('
            };
            this.path = 'assets/question.php'
        } catch(e) {}
    }

    initMask() {

        let setCursorPosition = (pos, elem) => {
            elem.focus();
            
            if (elem.setSelectionRange) {
                elem.setSelectionRange(pos, pos);
            } else if (elem.createTextRange) {
                let range = elem.createTextRange();

                range.collapse(true);
                range.moveEnd('character', pos);
                range.moveStart('character', pos);
                range.select();
            }
        };

        function createMask(event) {
            let matrix = '+1 (___) ___-____',
                i = 0,
                def = matrix.replace(/\D/g, ''),
                val = this.value.replace(/\D/g, '');

            if (def.length >= val.length) {
                val = def;
            }

            this.value = matrix.replace(/./g, function(a) {
                return /[_\d]/.test(a) && i < val.length ? val.charAt(i++) : i >= val.length ? '' : a;
            });

            if (event.type === 'blur') {
                if (this.value.length == 2) {
                    this.value = '';
                }
            } else {
                setCursorPosition(this.value.length, this);
            }
        }

        let inputs = document.querySelectorAll('[name="phone"]');

        inputs.forEach(input => {
            input.addEventListener('input', createMask);
            input.addEventListener('focus', createMask);
            input.addEventListener('blur', createMask);
        });
    }


    async postData(url, data) {
        const res = await fetch(url, {
            method: 'POST',
            body: data
        });
        const json = await res.text()
        return json;
    }
    clearInputs() {
        this.inputs.forEach(item => {
            item.value = '';
        });
    }

    clearTextInputs() {
        document.querySelectorAll('[name="email"]').forEach(input => {
            input.addEventListener('input', (e) => {
                input.value = input.value.replace(/[^a-z 0-9 @ \. \-]/ig, '');
            });
        });
    }

    init() {
        
        this.clearTextInputs();
        this.initMask()


        this.forms.forEach(form => {
            form.addEventListener('submit', (e) => {
            
                e.preventDefault();
                console.log(e.target);
                


                
    
                let messageBlock = document.createElement('div');
                messageBlock.textContent = this.messages.loading;
                messageBlock.style.fontSize = '20px';


                if(e.target.closest('.schedule__form')) {
                    messageBlock.style.color = 'black';
                } else {
                    messageBlock.style.color = 'white';
                }
    
                form.append(messageBlock);
    
    
    
                this.postData(this.path, new FormData(e.target))
                    .then(res => {
                        console.log(res);
                        messageBlock.textContent = this.messages.success;
                    })
                    .catch(res => {
                        
                        messageBlock.textContent = this.messages.error;
                    })
                    .finally(() => {
                        this.clearInputs();
                        setTimeout(() => {
                            messageBlock.remove();
                        }, 3000);
                        
                    })
                
                
    
            });
        })

    }






}