
import Slider from './slider';


export default class SliderMini extends Slider {

    constructor(container, prev, next, activeClass, animate, autoplay) {
        super(container, prev, next, activeClass, animate, autoplay);
    }

    decorizeSlides(i = 0) {
        [...this.slides].forEach(slide => {
            slide.classList.remove(this.activeClass);
            if(this.animate) {
                slide.querySelector('.card__title').style.opacity = '.4';
                slide.querySelector('.card__controls-arrow').style.opacity = '0';
            }
        });


        if(!this.slides[0].closest('button')) {
            this.slides[0].classList.add(this.activeClass);
        }

        

        if(this.animate) {
            this.slides[i].querySelector('.card__title').style.opacity = '1';
            this.slides[i].querySelector('.card__controls-arrow').style.opacity = '1';
        }
    }


    nextSlide() {

        for(let i = 0; i < this.slides.length; i++) {

            if(this.slides[i + 1].tagName !== 'BUTTON') {
                this.container.appendChild(this.slides[0]);
                this.decorizeSlides();
                break;
            } else {
                this.container.appendChild(this.slides[i]);
                i--;
            }

        }
    }

    autoPlay() {
        let slideTimer = setInterval(() => this.nextSlide(), 5000);

        this.container.addEventListener('mouseover', () => {
            clearInterval(slideTimer);
        })
    
        this.container.addEventListener('mouseout', () => {
            slideTimer = setInterval(() => this.nextSlide(), 5000);
        })
    }
    

    bindTriggers() {
        
        this.next.addEventListener('click', () => {
            

            this.nextSlide();
        });

        this.prev.addEventListener('click', () => {

            for(let i = this.slides.length - 1; i > 0; i--) {
                if(this.slides[i].tagName !== 'BUTTON') {
                    let active = this.slides[i];
                    this.container.insertBefore(active, this.slides[0]);
                    this.decorizeSlides();
                    break;

                } 
            }
            
        });
    }

    init() {
        try {
            this.container.style.cssText = `
            display: flex;
            flex-wrap: wrap;
            overflow: hidden;
            align-items: flex-start;
            `;
            this.bindTriggers();
            this.decorizeSlides();



            if(this.autoplay) {
                this.autoPlay();
            }
        } catch(e){}
    }
}